<template>
  <div class="tab-control">
    <div
      v-for="(item, index) in titles"
      :key="index"
      class="tab-control-item"
      @click="clickHandle(index)"
    >
      <span :class="{ active: curIdx == index }">{{ item }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    titles: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  components: {},
  data() {
    return {
      curIdx: 0,
    };
  },
  computed: {},
  methods: {
    clickHandle(index) {
      this.curIdx = index;
      this.$emit("tabClick", index);
    },
  },
  created() {},
  mounted() {},
};
</script>

<style lang="less" scoped>
.tab-control {
  width: 100%;
  display: flex;
  text-align: center;
  font-size: 15px;
  background-color: #fff;
  height: 40px;
  line-height: 40px;
  .tab-control-item {
    flex: 1;

    .active {
      color: var(--color-hight-text);
      padding-bottom: 5px;
      border-bottom: 3px solid var(--color-tint);
    }
  }
}
</style>
